@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,500;0,600;1,100;1,200;1,300;1,500&display=swap);
@import url(https://fonts.googleapis.com/css?family=Muli&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
#root{
  text-align: center;
  cursor: url('https://uploads.codesandbox.io/uploads/user/b3e56831-8b98-4fee-b941-0e27f39883ab/Ad1_-cursor.png') 39 39, auto;
}
:root{


--dark-color-lightness:17%;
--light-color-lightness:95%;
--white-color-lightness:100%;


    --color-while:hsl(252,30%,var(--white-color-lightness));
    --color-light:hsl(252,30%,var(--light-color-lightness));
    --color-gray:hsl(252,15%,65%);
    --color-primary:hsl(252,75%,60%);
    --color-secondary:hsl(252,100%,90%);
    --color-sucess:hsl(120,95%,65%);
    --color-denger:hsl(0,95%,65%);
    --color-dark:hsl(252,30%,var(--dark-color-lightness));
    --color-black:hsl(252,30%,10%);
    


--border-radius:2rem;
--card-border-radius:1rem;
--btn-padding:0.6rem 2rem;
--search-padding:0.6rem 1rem;
--card-padding:1rem;


--sticky-top-left:5.4rem;
--sticky-top-right:-18rem;



}
*,*::before,*::after{
    margin: 0;
    padding: 0;
    outline: 0;
    box-sizing: border-box;
    text-decoration: none;
    list-style: none;
    border: none;
}
body{
    font-family: 'Poppins', sans-serif;
    color: hsl(252,30%,17%);
    color: var(--color-dark);
    background: hsl(252,30%,95%);
    background: var(--color-light);
    overflow-x: hidden;

}
.container{
    width:80%;
    margin: 0 auto;

}

.profile-picture{
    width: 30px;
    height: 30px;
    border-radius: 50%;   
}

.btn{
    display: inline-block;
    padding: 0.6rem 2rem;
    padding: var(--btn-padding);
    font-weight: 500;
    border-radius: 2rem;
    border-radius: var(--border-radius);
    -webkit-border-radius: 2rem;
    -webkit-border-radius: var(--border-radius);
    -moz-border-radius: 2rem;
    -moz-border-radius: var(--border-radius);
    -ms-border-radius: 2rem;
    -ms-border-radius: var(--border-radius);
    -o-border-radius: 2rem;
    -o-border-radius: var(--border-radius);
    cursor: pointer;
    transition: all 300ms ease;
    font-size: 0.9rem;
    -webkit-transition: all 300ms ease;
    -moz-transition: all 300ms ease;
    -ms-transition: all 300ms ease;
    -o-transition: all 300ms ease;
}
.btn:hover{
    opacity: 0.8;
}

.btn-primary{
    background: hsl(252,75%,60%);
    background: var(--color-primary);
    color: white;
}
.text-bold{
    font-weight: 500;
}
.text-muted{
    color: hsl(252,15%,65%);
    color: var(--color-gray);
}

/* -------------------------------------------------------------------------------- */

nav{
    width: 100%;
    background-color: hsl(252,30%,100%);
    background-color: var(--color-while);   
    padding: 0.7rem 0;
    position:fixed;
    top: 0;
    z-index: 10;
}
.container{
    display: flex;
    flex-direction: row;
    
    justify-content: space-around;
    
    
}
.search_bar{
    background: hsl(252,30%,95%);
    background: var(--color-light);
    border-radius: 2rem;
    border-radius: var(--border-radius);
    -webkit-border-radius: 2rem;
    -webkit-border-radius: var(--border-radius);
    -moz-border-radius: 2rem;
    -moz-border-radius: var(--border-radius);
    -ms-border-radius: 2rem;
    -ms-border-radius: var(--border-radius);
    -o-border-radius: 2rem;
    -o-border-radius: var(--border-radius);
    padding: 0.6rem 1rem;
    padding: var(--search-padding);
    display: flex;
    flex-direction: row;
}


.search_bar input[type="search"]{
    background: transparent;
    width: 30vw;
    margin-left: 1rem;
    font-size: 0.9rem;
    color: hsl(252,30%,17%);
    color: var(--color-dark);

}
.search_bar input[type="search"]::-webkit-input-placeholder{
    color: hsl(252,15%,65%);
    color: var(--color-gray);
}
.search_bar input[type="search"]:-ms-input-placeholder{
    color: hsl(252,15%,65%);
    color: var(--color-gray);
}
.search_bar input[type="search"]::placeholder{
    color: hsl(252,15%,65%);
    color: var(--color-gray);
}

nav .create{

}


small{
    color: gray;

}



/* --------------------------------------------Main Section------------------------------------------ */

.main{
position: relative;
   
    top: 5.4rem;
  

}
.main .container {
   display: grid;
   grid-template-columns: 15vw auto 22vw;
   grid-column-gap: 2rem;
   -webkit-column-gap: 2rem;
           column-gap: 2rem;
  
  
   
   position: relative;
 
   
}



/* -------------------------------LEFT------------------------------------------------------- */


.left{
    height: -webkit-max-content;
    height: max-content;
   
    position: -webkit-sticky;
   
    position: sticky;

    top: 5.4rem;

    top: var(--sticky-top-left);
    
}

.profile{
    padding: 1rem;
    padding: var(--card-padding);
    background: hsl(252,30%,100%);
    background: var(--color-while);
    border-radius: 1rem;
    border-radius: var(--card-border-radius);
    -webkit-border-radius: 1rem;
    -webkit-border-radius: var(--card-border-radius);
    -moz-border-radius: 1rem;
    -moz-border-radius: var(--card-border-radius);
    -ms-border-radius: 1rem;
    -ms-border-radius: var(--card-border-radius);
    -o-border-radius: 1rem;
    -o-border-radius: var(--card-border-radius);
    display: flex;
    align-items: center;
    grid-column-gap: 1rem;
    -webkit-column-gap: 1rem;
            column-gap: 1rem;
}

.left .sidebar{ margin-top: 1rem;
     background: hsl(252,30%,100%);
     background: var(--color-while); 
     border-radius: 1rem; 
     border-radius: var(--card-border-radius); -webkit-border-radius: 1rem; -webkit-border-radius: var(--card-border-radius); -moz-border-radius: 1rem; -moz-border-radius: var(--card-border-radius); -ms-border-radius: 1rem; -ms-border-radius: var(--card-border-radius); -o-border-radius: 1rem; -o-border-radius: var(--card-border-radius);

}
.left .sidebar .menu-item{
    display: flex;
    align-items: center;
    height: 4rem;
    cursor: pointer;
    transition: all 300ms ease;
    position: relative;
    -webkit-transition: all 300ms ease;
    -moz-transition: all 300ms ease;
    -ms-transition: all 300ms ease;
    -o-transition: all 300ms ease;
}
.left .sidebar .menu-item:hover{
    background: hsl(252,30%,95%);
    background: var(--color-light);

}

.left .sidebar span{
font-size: 1.4rem;
color: hsl(252,15%,65%);
color: var(--color-gray);
margin-left: 2rem;
}

.left .sidebar span .notification-count{
    background: hsl(0,95%,65%);
    background: var(--color-denger);
    color: hsl(252,30%,100%);
    color: var(--color-while);
    font-size: 00.7rem;
    border-radius: 0.8rem;
    -webkit-border-radius:;
    -moz-border-radius:;
    -ms-border-radius:;
    -o-border-radius:;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    padding: 0.1rem 0.4rem;
    position: absolute;
    top: -0.05rem;
}

.left .sidebar h3{
    margin-left: 1.5rem;
    font-size: 1rem;
}

.sidebar .active{
    background: hsl(252,30%,95%);
    background: var(--color-light);
}
.sidebar .active span,.active h3{
    color: hsl(252,75%,60%);
    color: var(--color-primary);
}

.left .sidebar .active::before{
   content: "";
   display: block;
   
   width: 0.5rem;
   height: 100%;
   position: absolute;
   background: hsl(252,75%,60%);
   background: var(--color-primary);
}

.sidebar .menu-item:first-child.active{
    border-top-left-radius: 1rem;
    border-top-left-radius: var(--card-border-radius);
    overflow: hidden;
}

.left .btn{
    margin-top: 1rem;
    width: 100%;
    text-align: center;
    padding: 1rem 0;
}




/* ------------------Notification Popup---------------------------- */

.left .notification-popup{
     position: absolute;
     top: 0;
     display: flex;
     flex-direction: column;
     left: 110%;
     width: 30rem;
     background: hsl(252,30%,100%);
     background: var(--color-while);
     border-radius: 1rem;
     border-radius: var(--card-border-radius);
     padding: 1rem;
     padding: var(--card-padding);
     box-shadow: 0 0 2rem hsl(hsl(252,75%,60%),75%,60% 25%);
     box-shadow: 0 0 2rem hsl(var(--color-primary),75%,60% 25%);
     z-index: 8;
     display: none;
     -webkit-border-radius: 1rem;
     -webkit-border-radius: var(--card-border-radius);
     -moz-border-radius: 1rem;
     -moz-border-radius: var(--card-border-radius);
     -ms-border-radius: 1rem;
     -ms-border-radius: var(--card-border-radius);
     -o-border-radius: 1rem;
     -o-border-radius: var(--card-border-radius);
}

.left .notification-popup::before{
    content: "";
    width: 1.2rem;
    height: 1.2rem;
    display: block;
    background-color: hsl(252,30%,100%);
    background-color: var(--color-while);
    position: absolute;
    left: -0.6rem;
    transform: rotate(45deg);
    -webkit-transform: rotate(45deg);
    -moz-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    -o-transform: rotate(45deg);
}
.left .notification-popup >div{
    display: flex;
    align-items: start;
    grid-gap: 1rem;
    gap: 1rem;
    margin-bottom: 1rem;
}
.left .notification-popup small{
    display: block;
}

/* -------------------------Center____---------------------------------- */
.center{
    max-width: 33rem;
}
.center .stories{
    display: flex;
    justify-content: space-between;
    height: 12rem;
    grid-gap: 0.5rem;
    gap: 0.5rem;
   

}

.center .stories .story{
    padding: 1rem;
    padding: var(--card-padding);
    border-radius: 1rem;
    border-radius: var(--card-border-radius);
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    color: var(hsl(var(--color-primary),75%,60% 25));
    font-size: 0.75rem;
    width: 100%;
    position: relative;
    overflow:hidden ;
    color: white;
    color: hsl(252,30%,100%);
    color: var(--color-while);
    -webkit-border-radius:;
    -moz-border-radius:;
    -ms-border-radius:;
    -o-border-radius:;
}



.center .stories .story::before{
    content: "";
    display: block;
    width: 100%;
    height: 5rem;
    background: linear-gradient(transparent,
    rgba(0,0,0,0.85));

    position: absolute;
    bottom: 0;
 
}
.center .stories .story .name{
    z-index: 2;
    color: hsl(252,30%,100%);
    color: var(--color-while);
}

/* ------------------------------------------------------------------------------------------ */
.center .stories .story:nth-child(1){
    background: url('data:image/jpeg;base64,')
    no-repeat center/cover;
}
.center .stories .story:nth-child(2){
    background: url('data:image/jpeg;base64,')
    no-repeat center/cover;
}

.center .stories .story:nth-child(3){
    background: url('https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcQwSoQLn8t4J8qK3Hxf7HZoCt8o4bbPXbdypA&usqp=CAU')
    no-repeat center/cover;
}

.center .stories .story:nth-child(4){
    background: url('https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcQIbfDzNPtnPQF6u02N9c4z9QvRUPlIFGu91A&usqp=CAU')
    no-repeat center/cover;
}
.center .stories .story:nth-child(5){
    background: url('https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcRvOyPqZMkRTUHuDaLkHnHTaLni0r7ELyHCZg&usqp=CAU')
    no-repeat center/cover;
}






.center .stories .story .profile-picture{
    width: 2rem;
    height: 2rem;
    align-self: start;
    border: 3px solid hsl(252,75%,60%);
    border: 3px solid var(--color-primary);
}



/* -------------------------------------------------------------------------------------------------------- */


.center .create-post{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 1rem;
    background: hsl(252,30%,100%);
    background: var(--color-while);
    padding: 0.4rem 1rem;
    padding: 0.4rem var(--card-padding);
    border-radius:2rem;
    border-radius:var(--border-radius) ;
    -webkit-border-radius:;
    -moz-border-radius:;
    -ms-border-radius:;
    -o-border-radius:;
}

.center .create-post input[type="text"]{
    justify-self: start;
    width: 100%;
    padding-left: 1rem;
    background: transparent;
    color: hsl(252,30%,17%);
    color: var(--color-dark);
    margin-right: 1rem;

}










/* ----------------feeds--------------------------------------------- */


.center .feeds .feed{
    background: hsl(252,30%,100%);
    background: var(--color-while);
    border-radius: 1rem;
    border-radius: var(--card-border-radius);
    -webkit-border-radius:;
    -moz-border-radius:;
    -ms-border-radius:;
    -o-border-radius:;
    padding: 1rem;
    padding: var(--card-padding);
    margin: 1rem 0;
    font-size: 0.85rem;
    line-height: 1.5;
}

.center .feeds .feed .head{
    display: flex;
    justify-content: space-between;
}

.center .feeds .feed .head .user{
    display: flex;
    grid-gap: 1rem;
    gap: 1rem;
}

.center .feeds .feed .photo{
    border-radius: 1rem;
    border-radius: var(--card-border-radius);
    -webkit-border-radius:;
    -moz-border-radius:;
    -ms-border-radius:;
    -o-border-radius:;
    overflow: hidden;
    margin: 0.75rem;
}
.center .feeds .feed .photo img{
max-height: 360px;
object-fit: fill;
width: 100%;
}

.center .feeds .feed .action-buttons{
 display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 1.4rem;
    margin: 0.6rem;

}

.center .feeds .feed .liked-by{
    display: flex;

}
.center .feeds .feed .liked-by img{
    width: 1.4rem;
    height: 1.4rem;
    border-radius: 50%;
    -webkit-border-radius:;
    -moz-border-radius:;
    -ms-border-radius:;
    -o-border-radius:;
    display: block;
    overflow: hidden;
    border: 2px solid hsl(252,30%,100%);
    border: 2px solid var(--color-while);
    margin-left: -0.6rem;
}

.center .feeds .feed .liked-by p{
    margin-left: 0.5rem;
}



/* ---------------------Right------------------------------ */

.right{
    height: -webkit-max-content;
    height: max-content;
   
    position: -webkit-sticky;
   
    position: sticky;

    top: 5.4rem;

    top: var(--sticky-top-left);
}

.messages{
    background: hsl(252,30%,100%);
    background: var(--color-while);
    border-radius: 1rem;
    border-radius: var(--card-border-radius);
   
    padding: 1rem;
   
    padding: var(--card-padding);
    
}

.right .messages .heading{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 1rem;
  
}


.right .messages .search_bar {
    
    display: flex;
margin-bottom: 1rem;

}
.right .messages .search_bar .icon{
    font-size: 1.4rem;
}

.right .messages .category{
    display: flex;
    justify-content: space-between;
    margin-bottom: 1rem;

}

.right .messages .category h6{
    width: 100%;
    text-align: center;
    border-bottom: 4px solid hsl(252,30%,95%);
    border-bottom: 4px solid var(--color-light);
    padding-bottom: 0.5rem;
    font-size: 0.5rem;
}


.right .messages .category .active{
    border-color: hsl(252,30%,17%);
    border-color: var(--color-dark);
}

.message{
    display: flex;
    grid-gap: 1rem;
    gap: 1rem;
    margin: 1rem;
    align-items: start;
    position: relative;
    padding: 2px 10px;
    border-radius: 1rem;
    border-radius: var(--card-border-radius);
    transition: 0.3s;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    -webkit-transition: 0.3s;
    -moz-transition: 0.3s;
    -ms-transition: 0.3s;
    -o-transition: 0.3s;
    -webkit-border-radius: 1rem;
    -webkit-border-radius: var(--card-border-radius);
    -moz-border-radius: 1rem;
    -moz-border-radius: var(--card-border-radius);
    -ms-border-radius: 1rem;
    -ms-border-radius: var(--card-border-radius);
    -o-border-radius: 1rem;
    -o-border-radius: var(--card-border-radius);
}

.right .messages .message :last-child{
    margin: 0;
}

 .messages .message p{
    font-size: 1rem;
}

.right .messages .message .active{
    width: 0.8rem;
    height: 0.8rem;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
    position: absolute;
    right: 0;
    top: 50%;
    
    background: hsl(120,95%,65%);
    
    background: var(--color-sucess);

}




/* -----------------------Request--------------------------- */

.right .friend-request{
    margin-top: 1rem;
}


.right .friend-request h4{
    color: gray;
    margin: 1rem 0;
}
.right .request{
    background: hsl(252,30%,100%);
    background: var(--color-while);
    padding: 1rem;
    padding: var(--card-padding);
    border-radius: 2rem;
    border-radius: var(--border-radius);
    -webkit-border-radius: 2rem;
    -webkit-border-radius: var(--border-radius);
    -moz-border-radius: 2rem;
    -moz-border-radius: var(--border-radius);
    -ms-border-radius: 2rem;
    -ms-border-radius: var(--border-radius);
    -o-border-radius: 2rem;
    -o-border-radius: var(--border-radius);
    margin-bottom: 0.7rem;
}
.right .request .info{
    display: flex;
    grid-gap: 1rem;
    gap: 1rem;
    margin-bottom: 1rem;

}

.right .request .action{
    display: flex;
    grid-gap: 1rem;
    gap: 1rem;
}

















/* --------------------Media Query-------------------------------------------------------------------------------------------- */

@media screen and (max-width:1200px) {
    
.container{
    width: 96%;
}
.main .container{
    grid-template-columns: 5rem auto 25vw;
   grid-gap: 1rem;
   gap: 1rem;
}

.left{
    width: 5rem;
    z-index: 5;
}

.main .container .left .profile{
    display: none;
}
.sidebar h3{
display: none;
}
.left .btn{
    display: none;
}
}




/* -----------Modbile View------------ */

@media screen and (max-width:992px) {
    nav .search_bar{
        display: none;
    }
    .main .container{
grid-template-columns: 0 100% 0;
grid-gap: 0;
gap: 0;

    }
    .main .container .left{
        width: 4rem;
        grid-column: 3/4;
       position: fixed;
       right: 0;
        
        bottom: 0;
        
       
    }
   
    .main .container .center{
        grid-column: 1/3;
       
        top: 0;
        bottom: 0;
        
       
    }
    .main .container .center .feeds{
        
    }
    .main .container .center .feeds .feed{
       padding: 1rem;
    }
    
    .main .container .right{
        display: none;
    }

/* ----------------------Notification---------- */

.left .notification-popup{
    position: absolute;
  
    top: 1.3rem;
    left: -20rem;
    width: 20rem;
    
}

.notification-popup::before{
    position:absolute;
    top: 1.3rem;
    left: calc(20rem - 0.6rem);
    display: block;

}
.left .sidebar .menu-item{
    height: 4rem;
}
.left .sidebar span .notification-count{
    left: 0;
    
    font-size: 00.6rem;
}

.left .sidebar span{
    font-size: 1.4rem;
    color: hsl(252,15%,65%);
    color: var(--color-gray);
    margin-left: 1.2rem;
    }
}






