            animation: animate 5.5s linear forwards;
}
@-webkit-keyframes animate{
    0%{
        left: -20%;

    }
    100%{
        left: 120%;
        
    }
}
@keyframes animate{
    0%{
        left: -20%;

    }
    100%{
        left: 120%;
        
    }
}
h2 span{
    color: #ff022c;
}















 .reg_box {
    margin-top: 15px;
    background: #ffffff85;
    border-radius: 8px;
    padding: 15px;
    box-shadow: 2px 2px 5px #5d5e5e33;
}

 .max_width{
